body::-webkit-scrollbar {
  width: 8px;
}

body::-webkit-scrollbar-track {
  border-radius: 999px;
  margin: 10px 0px;
}

body::-webkit-scrollbar-thumb {
  background: rgb(200, 200, 200);
  border-radius: 999px;
}

body::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
}

.typewriter {
  overflow: hidden;
  border-right: 0.1em solid orange;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.15em;
  animation: typing 10s steps(300, end) infinite,
    blink-caret 800ms steps(44) infinite normal;
}

/* The typing effect */
@keyframes typing {
  0% {
    width: 0;
  }
  40% {
    width: 105%;
  }
  80% {
    width: 105%;
  }
  100% {
    width: 0;
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}
